import { Components } from '@ionic/core';
import { defineCustomElement as defineCustomElementToggle } from '@ionic/core/components/ion-toggle';
import { defineCustomElement as defineCustomElementText } from '@ionic/core/components/ion-text';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TpIonText } from '../../../../../../components/ionic/text/utils/text.type';
import {
  TP_COLORS,
  TP_COLORS_MEDSOFT,
  TP_COLORS_RECURSOS,
} from '../../../../../../utils/constants/color.constant';
import {
  TpColorAula,
  TpColorMaterial,
  TpColorOrange,
  TpColorProvas,
  TpColorQuestoes,
  TpColorRevalida,
} from '../../../../../../utils/enums/color.enum';
import { TpIonToggle } from '../../../utils/toggle.type';

defineCustomElementToggle();
defineCustomElementText();

const meta: Meta<
  Components.IonToggle & Components.IonText & TpIonToggle & TpIonText
> = {
  title: 'components/Ionic/Toggle/Website',
  argTypes: {
    color: {
      control: { type: 'select' },
      options: [...TP_COLORS, ...TP_COLORS_MEDSOFT, ...TP_COLORS_RECURSOS],
      description: `Define a cor do componente.

**OBS**: As cores **'${TpColorAula.Aula}'**, **'${TpColorMaterial.Material}'**, **'${TpColorQuestoes.Questoes}'**, **'${TpColorRevalida.Revalida}'** e **'${TpColorProvas.Provas}'** estão disponiveis apenas no theme **MedSoft**.

**OBS**: A cor **'${TpColorOrange.Orange}'** está disponivel apenas no theme **Recursos**.`,
      table: {
        defaultValue: {
          summary: 'undefined',
        },
        type: {
          summary: [
            ...TP_COLORS,
            ...TP_COLORS_MEDSOFT,
            ...TP_COLORS_RECURSOS,
          ].join('|'),
        },
      },
    },
  },
};

export default meta;

type Story = StoryObj<
  Components.IonToggle & Components.IonText & TpIonToggle & TpIonText
>;

export const DefaultMarkup: Story = {
  render: () => html`
    <ion-toggle mode="ios"></ion-toggle>

    <ion-toggle mode="ios" disabled></ion-toggle>
  `,
};
DefaultMarkup.argTypes = {
  color: { table: { disable: true } },
};

export const TextMarkup: Story = {
  render: () => html`
    <ion-toggle mode="ios">
      <ion-text tp-type="p16b">
        <p>Lorem ipsum, dolor sit amet consectetur.</p>
      </ion-text>
    </ion-toggle>
  `,
};
TextMarkup.argTypes = {
  color: { table: { disable: true } },
};

export const ColorsMarkup: Story = {
  render: ({ ...args }) => html`
    <ion-toggle mode="ios" color="${args.color}"></ion-toggle>
  `,
};
